import homeListItem from '@/components/home/homeListItem.vue';
import homeInfo from '@/components/home/homeInfo.vue';
import otherLogin from "@/components/home/otherLogin.vue"
import homeData from '@/components/home/homeData.vue';
export default {
	components: {
		homeListItem,
		homeInfo,
		homeData,
		otherLogin
	},
	data() {
		return {
			isLogin: false,
			list: [{
					icon: 'liulan',
					name: '浏览历史',
					clicktype: '',
					url: ''
				},
				{
					icon: 'huiyuanvip',
					name: '糗百认证',
					clicktype: '',
					url: ''
				},
				{
					icon: 'keyboard',
					name: '审核糗事',
					clicktype: '',
					url: ''
				}
			],
			homeInfo: {
				userpic: '/static/demo/demo66.jpg',
				username: '昵称',
				totalnum: 2,
				todaynum: 1
			},
			homeData: [{
					name: '糗事',
					num: 0
				},
				{
					name: '动态',
					num: 0
				},
				{
					name: '评论',
					num: 0
				},
				{
					name: '收藏',
					num: 0
				}
			],
			providerList: [], //当前端登录渠道
		};
	},
	onLoad() {
		this.getProvider()
		setTimeout(()=>{
			console.log(this.providerList)
		},200)
	},
	onNavigationBarButtonTap(e) {
		switch (e.index) {
			case 0:
				// console.log('设置页');
				uni.navigateTo({
					url: "../userSet/userSet"
				})
				break;
		}
	},
	methods: {
		openLogin() {
			uni.navigateTo({
				url: "../login/login"
			})
		},
		getProvider() {
			uni.getProvider({
				service: "oauth",
				success: (res) => {
					console.log(res)
					this.providerList = res.provider.map((value) => {
						let providerName = '';
						let icon = '';
						switch (value) {
							case 'weixin':
								providerName = "微信登录";
								icon = "weixin"
								break;
							case 'qq':
								providerName = "QQ登录";
								icon = "QQ"
								break;
							case 'sinaweibo':
								providerName = "新浪微博登录";
								icon = "xinlangweibo"
								break;
						}
						return{
							name:providerName,
							id:value,
							icon:icon
						}
					})
				}
			})
		}
	}
};
